import React, { useState, useRef } from 'react'
import './Modulo.css';
import styled from 'styled-components';
import Advantages from '../Advantages';
import Details from './Details';
import { Module1 } from './Content';
import { Box, Topic, CheckIcon } from './DetailsCss';
import { FaCheck } from 'react-icons/fa'
import Icon from './Icon';

const Modulo = (props) => {

    const [active, setActive] = useState("");
    const [height, setHeight] = useState("0px");
    const [rotate, setRotate] = useState("module__icon")

    const content = useRef(null);

    const toggle = () => {
        setActive(active === "" ? "active" : "");
        setHeight(active === "active" ? "0px" : `${content.current.scrollHeight}px`);
        setRotate(active === "active" ? "module__icon" : "module__icon rotate");
    }

    const topics= [];

    for(let i = 1; i < props.module.length; i++)
    {
        topics.push(
            <Topic>
                <CheckIcon><FaCheck/></CheckIcon>
                <p>{props.module[i]}</p>                            
            </Topic>    
        )

    }

    return (
        // <Container>
            <div className="module__section">
                <button className={`module ${active}`} onClick={toggle}>
                    <p className="module__title">{props.module[0]}</p>
                    <Icon className={`${rotate}`} width={15} fill={"#2e2592"} height={"auto"}/>
                </button>
                <div className="module__content" ref={content} style={{maxHeight: `${height}`}}>
                    {/* <div className="module__text"  dangerouslySetInnerHTML={{__html: props.text}}/> */}
                    <div className="module__text">
                        {/* <Details module={Module1}/> */}
                        <Box>
                            {topics}    
                        </Box>

                    </div>
                </div>
            </div>         
        // </Container>

    )
}

export default Modulo
