import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, {Navigation, Pagination} from 'swiper';
import 'swiper/swiper-bundle.css'
import { FeedbacksContainer, SwiperContainer, ImgWrapper, FeedbacksH1 } from './Feedbacks';
import Slide1 from './Feedback1.jpeg';
import Slide2 from './Feedback2.jpeg';
import Slide3 from './Feedback3.jpeg';
import Slide4 from './Feedback4.jpeg';
import Slide5 from './Feedback5.jpeg';
import Slide6 from './Feedback6.jpeg';
import './Feedbacks.css'

SwiperCore.use([Navigation, Pagination]);

const Feedbacks = () => {

    const images = [Slide1, Slide2, Slide3, Slide4, Slide5, Slide6];

    const slides = [];

    for(let i = 0; i < 6; i++)
    {
        slides.push(
            <SwiperSlide tag='li' key={`slide-${i}`} 
            style={{listStyle: 'none', width: '100%', height: 'auto'}}
            >
                <ImgWrapper>
                    <img id='feedback' src={images[i]} alt={`slide-${i}`} 
                    // width='100%' height='auto' style={{borderRadius: '5px solid'}}
                    />
                </ImgWrapper>
            </SwiperSlide>
        )
    }

    return (
           
        <FeedbacksContainer>
            <FeedbacksH1>Feedbacks:</FeedbacksH1>
        <React.Fragment>
            {/* <SwiperContainer> */}        
            <Swiper tag='section' wrapperTag='ul' id='main' navigation pagination spaceBetween={0} slidesPerView={1}>{slides}</Swiper>    
            {/* </SwiperContainer>              */}
        </React.Fragment>  
        </FeedbacksContainer>
      
    )
}

export default Feedbacks;