import React, { useState, useRef } from 'react'
import './Question.css';
import styled from 'styled-components';
import Chevron from './Chevron';

// const Container = styled.div`
//     display: flex;
//     flex-direction: column;
//     justify-content: center;
//     align-items: center;
//     background: #2e2592;
//     padding: 70px 70px;

// `

const Question = (props) => {

    const [active, setActive] = useState("");
    const [height, setHeight] = useState("0px");
    const [rotate, setRotate] = useState("accordion__icon")

    const content = useRef(null);

    const toggle = () => {
        setActive(active === "" ? "active" : "");
        setHeight(active === "active" ? "0px" : `${content.current.scrollHeight}px`);
        setRotate(active === "active" ? "accordion__icon" : "accordion__icon rotate");
    }

    return (
        // <Container>
            <div className="accordion__section">
                <button className={`accordion ${active}`} onClick={toggle}>
                    <p className="accordion__title">{props.title}</p>
                    <Chevron className={`${rotate}`} width={10} fill={"#2d23a6"} height={"auto"}/>
                </button>
                <div className="accordion__content" ref={content} style={{maxHeight: `${height}`}}>
                    <div className="accordion__text" dangerouslySetInnerHTML={{__html: props.text}}/>
                </div>
            </div>         
        // </Container>

    )
}

export default Question
